<template>
  <div class="page flex-v">
    <div class="head flex-v-center flex-justify">
      <el-breadcrumb separator-class="el-icon-arrow-right" class="my-breadcrumb">
        <el-breadcrumb-item :to="{ name: 'RoleList' }">角色管理</el-breadcrumb-item>
        <el-breadcrumb-item>关联权限</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="all-checkbox flex-v-center">
      <span class="iconfont bj-suiji"></span>
      <span class="role-name">{{roleName}}</span>
      <el-checkbox class="green" v-model="isAllSelect"
        @change='handleAllSelect'>全选</el-checkbox>
    </div>
    <div class="container flex-item">
      <el-tree :data="allMenuList" 
        :props="defaultProps"
        @check='onCheckbox'
        show-checkbox 
        class="tree" 
        node-key="menuId" 
        ref="tree"></el-tree>
      <div class="btns-box">
        <el-button class="btn-hollowing" @click="$router.back()">取消</el-button>
        <el-button class="btn-info" @click="onSubmit" :loading="submitLoading">保存</el-button>
      </div> 
    </div>

  </div>
</template>


<script>
  export default {
    name: 'AssignPermissions',
    props: {
      roleId: {
        type: String,
        required: true
      },
      roleName: {
        type: String,
        required: true
      }
    },
    data() {
      return {
        allMenuList: [],
        roleMenuList: [],
        defaultProps: {
          label: 'menuName'
        },

        allSelectIds: [],
        isAllSelect: false,
        submitLoading: false
      }
    },

    created () {
      
    },
    mounted() {
      this.getMenuById()
    },

    methods: {
      getMenuById() {
        this.$post('/role/getMenuByRole', {roleId: this.roleId}).then(({ data }) => {
          this.allMenuList = data.menuListByAccout
          this.initSelectStatus(data.resultByRole)
        }).catch(err => this.$message.error(err))
      },

      initSelectStatus(roleMenuList) {
        if(!roleMenuList.length) return
        let roleMenuIds = []
        ;(function handleArr(list) {
          list.forEach(item => {
            if(item.children) 
              return handleArr(item.children)
            roleMenuIds.push(item.menuId)
          })
        })(roleMenuList)
        let tree = this.$refs.tree
        tree.setCheckedKeys(roleMenuIds)
        this.$nextTick(() => { 
          // 是否全选
          let checkedKeys = tree.getCheckedKeys()
          let selectMainMenu = this.allMenuList.filter(item => ~checkedKeys.indexOf(item.menuId))
          this.isAllSelect = selectMainMenu.length === this.allMenuList.length
        })
      },

      getSelectMenuIds() {
        let tree = this.$refs.tree
        return tree.getCheckedKeys().concat(tree.getHalfCheckedKeys())
      },

      handleAllSelect(val) {
        let tree = this.$refs.tree
        val ? tree.setCheckedKeys(this.allMenuList.map(item => item.menuId)) :
          tree.setCheckedKeys([])
      },

      onCheckbox(current, obj) {
        let { checkedKeys } = obj
        let selectMainMenu = this.allMenuList.filter(item => ~checkedKeys.indexOf(item.menuId))
        this.isAllSelect = selectMainMenu.length === this.allMenuList.length
      },

      onSubmit() {
        let menus = this.getSelectMenuIds()
        if(!menus.length) return this.$message.warning('至少选择一项')
        this.submitLoading = true
        this.$post('/role/AddMenuByRole', { 
          roleId: this.roleId, 
          menus: menus.toString() 
        }).then(res => {
          this.submitLoading = false
          this.$router.back()
          this.$message.success('保存成功')
        }).catch(err => {
          this.submitLoading = false
          this.$message.error(err)
        })
      }
    }

    
  }
</script>

<style lang="less" scoped>
  .head {
    padding: 0 70px 0 20px;
    height: 70px;
    border-bottom: 1px solid #eaeff5;
  }

  .tree {
    /deep/ & > .el-tree-node {
      margin-bottom: 20px;
      & > .el-tree-node__content {
        height: 50px;
        background: #f7fcfc;
        & > .el-checkbox {
          margin-left: 18px;
        }
        & > .el-tree-node__expand-icon.el-icon-caret-right {
          position: absolute;
          right: 10px;
          font-size: 16px;
          &:before {
            content: '\e6df'
          }
          &.expanded {
            transform: rotate(-180deg);
            color: @btn-info
          }
        }
      }
    }
  }

  .all-checkbox {
    margin: 0 20px;
    height: 76px;
    border-bottom: 1px solid #eaeff5;
    span.iconfont {
      display: inline-block;
      width: 28px;
      height: 28px;
      line-height: 28px;
      text-align: center;
      border-radius: 50%;
      color: #fff;
      background: @color-red;
    }
    .role-name {
      margin: 0 20px 0 10px;
      font-size: 16px;
    }
  }

  .container {
    padding: 40px 200px;
    overflow-y: auto
  }
  .btns-box {
    margin-top: 80px;
    text-align: center
  }

